import React, { useEffect, useMemo, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Button, IconButton, Tab, Typography, Paper, InputBase } from "@material-ui/core";
import CampaignHeader from '../../components/Common/campaignHeader';
import { StyledTab, StyledTabs } from '../../components/Common/StyledTabs';
import SearchIcon from '@material-ui/icons/Search';
import AvailableTable from '../../components/Validator/AvailableTable';
import SubscribedTable from '../../components/Validator/SubscribedTable';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        height: 'calc(100vh - 103px)',
        backgroundColor: '#332B4F',
        padding: '30px 45px'
    },
    padding: {
        padding: theme.spacing(3),
    },
    header: {
        display: 'flex',
        marginTop: '24px',
        marginLeft: '24px',
        [theme.breakpoints.down('sm')]: {
            display: 'block'
        },
    },
    tabs: {
        display: 'flex',
        justifyContent: 'space-between',
        marginTop: 20
    },
    paper: {
        padding: '2px 5px',
        display: 'flex',
        alignItems: 'center',
        width: 300,
        backgroundColor: '#231C3D',
        borderColor: '#17122B',
        border: '1px solid #17122B',
    },
    iconButton: {
        padding: 10,
        color: 'white'
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
        color: 'white'
    },
    tabMain: {
    },
    buttons: {
        width: 200,
        height: 50,
        margin: 30
    }
}));

const Validator = (props) => {

    const classes = useStyles();


    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <CampaignHeader />
            <div className={classes.tabs}>
                <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
                    <StyledTab label="Subscribed" />
                    <StyledTab label="Available" />
                </StyledTabs>
                <Paper component="form" className={classes.paper}>
                    <InputBase
                        className={classes.input}
                        placeholder="Search..."
                        inputProps={{ 'aria-label': 'search google maps' }}
                    />
                    <IconButton className={classes.iconButton} aria-label="search">
                        <SearchIcon />
                    </IconButton>
                </Paper>
            </div>
            {
                value === 0 ? <SubscribedTable />
                    : <AvailableTable />
            }
        </div>
    )
}

export default Validator;