import { Button, Dialog, DialogTitle, Grid, IconButton, Typography, Popper, Grow, Paper, ClickAwayListener, MenuList, MenuItem } from "@material-ui/core";
import { useLocation } from 'react-router-dom';
import { useWeb3React } from "@web3-react/core";
import { connectMetam, askWallet, connectSolletWallet, connectPhantomWallet, setInitialize } from '../../utils/Connect-contract';
import { useConnection, useWallet, useAnchorWallet } from '@solana/wallet-adapter-react';
import {
    Program, Provider, web3
} from '@project-serum/anchor';
import { LAMPORTS_PER_SOL } from "@solana/web3.js";
import { Connection, PublicKey } from '@solana/web3.js';
import contractJson from '../../contracts/contracts.json'
import idjson from '../../contracts/id1.json'
import { getPhantomWallet } from '@solana/wallet-adapter-wallets';
import { WalletModalProvider, WalletMultiButton, WalletDisconnectButton } from '@solana/wallet-adapter-react-ui';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { shortenAddress } from '../../utils/convert'

import logo from "../../assets/img/logo.png";
import sns from "../../assets/img/sns.png";
import walletimg from "../../assets/img/wallet.png";

import { setRole, setInit, setProgram, setPoolFlag } from '../../redux/ducks/main';
import store from '../../redux';
import { useDispatch } from "react-redux";
import CloseIcon from '@material-ui/icons/Close';

const solanaWeb3 = require("@solana/web3.js");


const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: '#231C3D',
        padding: '25px 50px',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    },
    imglogo: {
    },
    buttonBox: {
        justifyContent: 'right'
    },
    walletButton: {
        width: 200,
        height: 50,
    },
    addressButton: {
        width: 450,
        height: 50,
    },
    btn: {
        backgroundColor: '#4A4361',
        fontSize: 18,
        fontWeight: 400,
        textTransform: 'none',
        marginRight: 20,
        paddingLeft: 30,
        paddingRight: 30,
        height: 50,
        color: 'white',
        '&:hover': {
            background: '#6A5E8E'
        }
    },
    btn1: {
        backgroundColor: '#08C477',
        fontSize: 18,
        textTransform: 'none',
        fontWeight: 400,
        fontStyle: 'normal',

        color: '#231C3D',
        paddingLeft: 30,
        height: 50,
        paddingRight: 30,
        '&:hover': {
            background: '#39D595'
        }
    },
    txtbal: {
        alignSelf: 'center',
        marginRight: 20,
        color: 'white'
    },
    headerDiv: {
        display: 'flex'
    },
    walletBtn: {
        height: 50
    },
    balDiv: {
        width: 240,
        height: 50,
        background: '#332B4F',
        borderRadius: 5,
        padding: 4,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginLeft: 40,
    },
    balDiv1: {
        width: 265,
        height: 50,
        background: '#332B4F',
        borderRadius: 5,
        padding: 4,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginLeft: 40,
        cursor: 'pointer'
    },
    paper: {
        width: 265,
        backgroundColor: '#453A6B',
    },
    snsimg: {
        paddingLeft: 10
    },
    valueDiv: {
        width: 180,
        height: '100%',
        backgroundColor: '#2A2344',
        borderRadius: 5,
        padding: '6px 15px',
        paddingRight: 0,
        display: 'flex',
        alignItems: 'center'
    },
    valueDiv1: {
        width: 200,
        height: '100%',
        backgroundColor: '#2A2344',
        borderRadius: 5,
        padding: 5,
        paddingRight: 5,
        display: 'flex',
        alignItems: 'center'
    },
    balTitle: {
        color: 'white',
        lineHeight: 1,
        fontSize: 14
    },
    valDiv: {
        width: '100%',
        textAlign: 'center'
    },

    valueTxt: {
        fontSize: 22,
        background: "-webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
        WebkitBackgroundClip: "text",
        WebkitTextFillColor: "transparent"
    },
    roletxt: {
        color: 'white',
        fontSize: 14
    },
    architectdiv: {
        backgroundColor: '#6E56F8',
        borderRadius: 5,
        width: 72,
        height: 22,
        textAlign: 'center'
    },
    btnDiv: {
        display: 'flex',
        paddingLeft: 5,
        alignItems: 'center',
        justifyContent: 'space-between',
        cursor: 'pointer'
    },
    address: {
        color: 'white',
        fontSize: 16
    },
    iconBtn: {
        color: 'white',
        padding: 0
    },
    dropitem: {
        textAlign: 'center',
        color: 'white',
        justifyContent: 'center'
    },
    dropdiv: {
        zIndex: 10
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialogDiv: {
        width: 180,
        height: 180,
        borderRadius: 6,
        cursor: 'pointer',
        textAlign: 'center',
        textTransform: 'none',
        display: 'block',
        background: '#514480',
    },
    dialogDivSelected: {
        textAlign: 'center',
        width: 180,
        height: 180,
        borderRadius: 6,
        cursor: 'pointer',
        textTransform: 'none',
        display: 'block',
        background: 'linear-gradient(91.6deg, #8B63F3 -54.15%, #E8487F 144.53%)',

    },
    contentDiv: {
        padding: 30
    },
    btnDivrole: {
        padding: 20
    },
    changeBtn: {
        marginTop: 20,
        textTransform: 'none',
        textAlign: 'center'
    },
    roleDiv: {
        color: '#453A6B'
    },
    dialogHeader: {
        fontSize: 36,
        color: 'white',
    },
    dialogTitle: {
        textAlign: 'center'
    },
    borderDivSelected: {
        width: 200,
        height: 200,
        padding: 2,
        background: "linear-gradient(90deg, #8B63F3 -11.75%, #E8487F 105.01%)",
        borderRadius: 6
    },
    borderDiv: {
        width: 200,
        height: 200,
        padding: 2,
        background: "#453A6B",
    },
    midDiv: {
        width: "100%",
        height: '100%',
        background: "#453A6B",
        borderRadius: 6,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    selectRole: {
        fontSize: 80
    },
    selectRoletxt: {
        fontSize: 24,
        marginTop: 23,
        textAlign: 'center'
    },
    btnChooseDiv: {
        width: '100%',
        textAlign: 'center'
    }
}));


function Header(props) {

    const { SystemProgram, Keypair } = web3;
    /* create an account  */

    const opts = {
        preflightCommitment: "processed"
    }
    const anchor = require('@project-serum/anchor');

    const programID = new PublicKey('GWzBR7znXxEVDkDVgQQu5Vpzu3a5G4e5kPXaE9MvebY2');

    const SNS = new PublicKey("51LAPRbcEvheteGQjSgAFV6rrEvjL4P2igvzPH8bu88");

    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);

    const [openRole, setOpenRole] = React.useState(false);
    const [selected, setSelected] = React.useState(store.getState().main.role);

    const dispatch = useDispatch();
    const location = useLocation();


    useEffect(() => {
        setSelected(store.getState().main.role)
    }, [store.getState().main.role])

    const handleClickOpen = () => {
        setOpenRole(true);
    };
    const handleRoleClose = () => {
        setOpenRole(false);
    };

    const handleSelectRole = (role) => {
        setSelected(role);
    }

    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const changeRole = () => {
        dispatch(setRole(selected));
        setOpenRole(false);
        if (selected === 0) {
            props.history.push('/architect')
        } else if (selected === 1) {
            props.history.push('/builder')
        } else {
            props.history.push('/validator')
        }
    }

    useEffect(() => {
        console.log('location.pathname', location.pathname)
        if (location.pathname === '/architect') {
            dispatch(setRole(0));
        } else if (location.pathname === '/builder') {
            dispatch(setRole(1));
        } else {
            dispatch(setRole(2));
        }
    }, [])

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };
    const handleCloseRole = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }
        setOpen(false);
        setOpenRole(true);
    };

    const CloseRole = () => {
        setOpenRole(false);
    }

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current.focus();
        }

        prevOpen.current = open;
    }, [open]);

    const classes = useStyles();
    const wallet = useWallet();
    const [connected, setConnected] = React.useState(false);


    const user = Keypair.generate();
    const admin = Keypair.generate();
    const customer = Keypair.generate();
    const customerB = Keypair.generate();
    const architect = Keypair.generate();
    const architectB = Keypair.generate();
    const builder = Keypair.generate();
    const validator = Keypair.generate();
    const new_authority = Keypair.generate();

    useEffect(() => {
        window.solana.on("connect", () => setConnected(true));
        return () => window.solana.disconnect();
    }, [])

    useEffect(() => {
        async function getProvider() {
            /* create the provider and return it to the caller */
            /* network set to local network for now */
            console.log('wallet', wallet);
            const network = "https://api.devnet.solana.com";
            const connection = new Connection(network, opts.preflightCommitment);

            const balance = await connection.getBalance(wallet.publicKey);
            console.log(balance / LAMPORTS_PER_SOL);

            const provider = new Provider(
                connection, wallet, opts.preflightCommitment,
            );
            const program = new Program(contractJson, programID, provider);
            dispatch(setInit(user, admin, customer, customerB, architect, architectB, builder, validator, new_authority, SNS, program));


            //create mining pool

            const architect_stake = new anchor.BN(20);
            const builder_stake = new anchor.BN(20);
            const validator_stake = new anchor.BN(20);
            const reward_apy = 10;
            const pool_cap = new anchor.BN(250000000);
            const penalty = new anchor.BN(2);

            try {
                const transaction = await program.rpc.initPool(
                    architect_stake, builder_stake, validator_stake,
                    reward_apy, pool_cap, penalty,
                    {
                        accounts: {
                            poolAccount: admin.publicKey,
                            poolAuthority: user.publicKey,
                            sns: SNS
                        },
                        signers: [admin],
                        instructions: [await program.account.poolAccount.createInstruction(admin)],

                    });
                const pool = await program.account.poolAccount.fetch(admin.publicKey);
                console.log('pool', pool);
            } catch (err) {
                console.log(" ", err);
            }

            //update mining pool apy

            const newApy = 13;
            try {
                const transaction = await program.rpc.updatePool(newApy, new_authority.publicKey,
                    {
                        accounts: {
                            poolAccount: admin.publicKey,
                            authority: admin.publicKey
                        },
                        signers: [admin]
                    });
                const pool = await program.account.poolAccount.fetch(admin.publicKey);
                console.log('pool', pool);
            } catch (err) {
                console.log(" ", err);
            }
            dispatch(setProgram(program))
            dispatch(setPoolFlag(true))
        }
        if (wallet.connected) {
            getProvider();
        }
    }, [wallet])




    return (
        <div className={classes.root}>
            <Link className={classes.sideIcon} to='/'>
                <img className={classes.imglogo} src={logo} alt="logo"></img>
            </Link>
            {
                !connected ? <WalletMultiButton className={classes.walletBtn} /> :
                    wallet.publicKey !== null && <div className={classes.headerDiv}>
                        <Button className={classes.btn} variant="contained" size="medium">Redeem rewards</Button>
                        <Button className={classes.btn1} variant="contained" size="medium">Get SNS</Button>
                        <div className={classes.balDiv}>
                            <img src={sns} alt='sns' className={classes.snsimg}></img>
                            <div className={classes.valueDiv}>
                                <Typography className={classes.balTitle}>Wallet <br /> ballence:</Typography>
                                <div className={classes.valDiv}>
                                    <Typography className={classes.valueTxt}>500 SNS</Typography>
                                </div>
                            </div>
                        </div>
                        <div className={classes.balDiv1} ref={anchorRef}
                            aria-controls={open ? 'menu-list-grow' : undefined}
                            aria-haspopup="true"
                            onClick={handleToggle}>
                            <img src={walletimg} alt='wallet' className={classes.snsimg}></img>
                            <div className={classes.valueDiv1}>
                                <div className={classes.architectdiv}>
                                    <Typography className={classes.roletxt}>{location.pathname === '/architect' ? 'Architect' : (location.pathname === '/builder' ? 'Builder' : 'Validator')}</Typography>
                                </div>
                                <div className={classes.btnDiv}>
                                    <Typography className={classes.address}>{shortenAddress(wallet.publicKey.toBase58() || "")}</Typography>
                                    <IconButton className={classes.iconBtn}><MoreVertIcon /></IconButton>

                                </div>
                            </div>
                        </div>
                        <Popper className={classes.dropdiv} open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                            {({ TransitionProps, placement }) => (
                                <Grow
                                    {...TransitionProps}
                                    style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                                >
                                    <Paper className={classes.paper}>
                                        <ClickAwayListener onClickAway={handleClose}>
                                            <MenuList autoFocusItem={open} id="menu-list-grow" >
                                                <MenuItem onClick={handleCloseRole} className={classes.dropitem}>Choose Role</MenuItem>
                                                <MenuItem onClick={handleClose} className={classes.dropitem}>Log out <ExitToAppIcon /></MenuItem>
                                            </MenuList>
                                        </ClickAwayListener>
                                    </Paper>
                                </Grow>
                            )}
                        </Popper>
                    </div>

            }
            <Dialog scroll='body' onClose={handleRoleClose} PaperProps={{
                style: {
                    backgroundColor: '#453A6B',
                    boxShadow: 'none',
                },
            }} aria-labelledby="customized-dialog-title" open={openRole} maxWidth='md'>
                <DialogTitle disableTypography className={classes.dialogTitle}>
                    <Typography className={classes.dialogHeader}>Choose role</Typography>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={CloseRole}>
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <div className={classes.contentDiv}>
                    <Grid container direction='row' justify="center"
                        alignItems="center" spacing={5}>
                        <Grid className={classes.chooseDiv} item>
                            <div className={selected === 0 ? classes.borderDivSelected : classes.borderDiv}>
                                <div className={classes.midDiv}>
                                    <Button
                                        onClick={() => handleSelectRole(0)}
                                        className={selected === 0 ? classes.dialogDivSelected : classes.dialogDiv}>
                                        <Typography className={classes.selectRole}>Arc</Typography>
                                    </Button>
                                </div>
                            </div>
                            <Typography className={classes.selectRoletxt}>Architect</Typography>

                        </Grid>
                        <Grid className={classes.chooseDiv} item>
                            <div className={selected === 1 ? classes.borderDivSelected : classes.borderDiv}><div className={classes.midDiv}>
                                <Button onClick={() => handleSelectRole(1)}
                                    className={selected === 1 ? classes.dialogDivSelected : classes.dialogDiv}>
                                    <Typography className={classes.selectRole}>Bld</Typography>
                                </Button>
                            </div>
                            </div>
                            <Typography className={classes.selectRoletxt}>Builder</Typography>
                        </Grid>
                        <Grid className={classes.chooseDiv} item>
                            <div className={selected === 2 ? classes.borderDivSelected : classes.borderDiv}><div className={classes.midDiv}>
                                <Button onClick={() => handleSelectRole(2)}
                                    className={selected === 2 ? classes.dialogDivSelected : classes.dialogDiv}>
                                    <Typography className={classes.selectRole}>Val</Typography>
                                </Button>
                            </div>
                            </div>
                            <Typography className={classes.selectRoletxt}>Validator</Typography>
                        </Grid>
                    </Grid>
                    <div className={classes.btnDivrole}>
                        <Typography>Architect: have to apply, manual approval process.Minimum SNS staking amount etc</Typography>
                        <div className={classes.btnChooseDiv}>
                            <Button className={classes.changeBtn} size='large' onClick={changeRole} variant="contained" color="primary">Change Role</Button>
                        </div>
                    </div>

                </div>
            </Dialog >

        </div >
    );
}



export default withRouter(Header)