import React, { useEffect, useMemo, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import theme from "../../utils/theme";
import {
    TableContainer, Table, TableHead,
    TableRow,
    TableCell,
    TableSortLabel,
    TableBody, TablePagination,
    Button,
    TextField,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    Box,
    Typography, Dialog, DialogTitle, IconButton, Grid
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Builder from '../../pages/Builder';
import { withRouter, useLocation } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        marginBottom: 40
    },
    table: {
        minWidth: 750,
        borderCollapse: 'separate',
        borderSpacing: '0px 5px',
    },
    tableHead: {
        color: 'grey',
        borderBottom: "none",
        paddingTop: 60,
        paddingBottom: 25
    },
    tblRow: {
        background: '#231C3D',
        '&:nth-child(even)': {
            background: '#292244'
        }
    },
    tblpagination: {
        color: 'white'
    },
    tableBody: {
        color: 'white',
        borderBottom: "none",
    },
    contentDiv: {
        padding: 30
    },
    valuedivs: {
        marginBottom: 5
    },
    txtTitle: {
        fontWeight: 'bold'
    },
    txtValue: {

    },
    organizeTxt: {
        color: '#8291F6'
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    detailtxt: {
        fontSize: 24
    },
    divider: {
        width: '100%',
        height: 2,
        background: 'linear-gradient(90deg, #8B63F3 -11.75%, #E8487F 105.01%)',
        marginBottom: 40
    },
    btns: {
        textTransform: 'none',
        width: 200,
        marginTop: 40,
        background: 'linear-gradient(90deg, #8B63F3 -11.75%, #E8487F 105.01%)',
        '&:hover': {
            background: 'linear-gradient(270deg, #8B63F3 -28.4%, #E8487F 167.84%)'
        },
        color: 'white',
    },
    btns1: {
        textTransform: 'none',
        color: 'white',
        borderColor: 'white',
        width: 200,
        marginTop: 40
    },
}));

const active_data = [{
    topic: 'eCommerce / Delivery times',
    organizer: 'ABS S.A.',
    total: '75',
    estimated: '5',
    limits: '5',
    apy: '15'
},
{
    topic: 'Telecom / data usage',
    organizer: 'LMN B.V.',
    total: '75',
    estimated: '3',
    limits: '7',
    apy: '12'
}]

const headCells = [
    { id: 'topic', numeric: true, disablePadding: true, label: 'Topic' },
    { id: 'organizer', numeric: false, disablePadding: true, label: 'Organizer' },
    { id: 'total', numeric: false, disablePadding: false, label: 'Total utterances' },
    { id: 'estimated', numeric: false, disablePadding: false, label: 'Estimated (days)' },
    { id: 'limits', numeric: false, disablePadding: false, label: 'Time limits (days)' },
    { id: 'apy', numeric: false, disablePadding: false, label: 'Estimated APY (%)' },
    { id: 'status', numeric: false, disablePadding: false, label: 'Status' },
];

function EnhancedTableHead(props) {
    const { classes, rowCount } = props;
    return (
        <TableHead className={classes.tableRow}>
            <TableRow>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align={headCell.numeric ? 'left' : 'center'}
                        padding={'default'}
                        className={classes.tableHead}
                    >
                        {headCell.label}
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}


const AvailableTable = (props) => {

    const classes = useStyles();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [selected, setSelected] = useState([]);
    const [display, setColor] = React.useState('contents');
    const [open, setOpen] = React.useState(false);


    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1),
            );
        }

        setSelected(newSelected);
    };
    return (
        <div className={classes.root}>
            <TableContainer>
                <Table
                    className={classes.table}
                    aria-labelledby="tableTitle"
                    size='medium'
                    aria-label="enhanced table"
                >
                    <EnhancedTableHead
                        classes={classes}
                        rowCount={active_data.length}
                    />
                    <TableBody>
                        {
                            active_data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
                                const labelId = `enhanced-table-checkbox-${index}`;
                                return (
                                    <TableRow
                                        hover
                                        onClick={(event) => handleClick(event, row.coin)}
                                        role="checkbox"
                                        tabIndex={-1}
                                        className={classes.tblRow}
                                        key={index}>
                                        <TableCell align="left" className={classes.tableBody}>{row.topic}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}><Typography className={classes.organizeTxt}>{row.organizer}</Typography></TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.total}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.estimated}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.limits}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.apy}</TableCell>
                                        <TableCell align="center" className={classes.tableBody} onClick={handleClickOpen}><Button variant='contained' color='primary'>Stake</Button></TableCell>
                                    </TableRow>
                                )
                            })
                        }
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                className={classes.tblpagination}
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={active_data.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
            <Dialog scroll='body' onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}
                PaperProps={{
                    style: {
                        backgroundColor: '#453A6B',
                        boxShadow: 'none',
                    },
                }} maxWidth='md'>
                <DialogTitle disableTypography>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <div className={classes.contentDiv}>
                    <Typography className={classes.detailtxt}>Create Campaign</Typography>
                    <div className={classes.divider} />
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Organizer</Typography>
                            <TextField value="XYZ Corp" className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                        <Grid item xs={3}>
                            <Typography className={classes.title}>Topic domain</Typography>
                            <TextField value="Insurance" className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                        <Grid item xs={3}>
                            <Typography className={classes.title}>Topic subject</Typography>
                            <TextField value="rates" className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={12}>
                            <Typography className={classes.title}>Topic subject</Typography>
                            <TextField value='This campaign to help XYZ corp better engage with users/customers about insurance rates, specifically the differences between flat and usage-based rates' className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={12}>
                            <Typography className={classes.title}>Example seed phrases</Typography>
                            <TextField value='With my current usage, what is my monthly rate with the usage-based plan? 
How much will I save with a usage-based plan? 
Can I switch back to a flat rate plan?' className={classes.inputfield} id="outlined-basic" size='small' multiline={true}
                                rows={3} fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Reward per utterance (SNS)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Required utterances (items)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Time limit (days)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Fixed Reward (%)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Mining Reward (%)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Minimum Validations (# of utterances)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid className={classes.inputdivs} container spacing={3}>
                        <Grid item xs={6}>
                            <Typography className={classes.title}>Validation Quorum (% of total)</Typography>
                            <TextField className={classes.inputfield} id="outlined-basic" size='small' fullWidth variant="outlined" />
                        </Grid>
                    </Grid>
                    <Grid container direction='row' justify="center" spacing={3}
                        alignItems="center" className={classes.valuedivs}>
                        <Grid item>
                            <Button className={classes.btns1} onClick={handleClose} variant='outlined' size='large'>Cancel</Button>
                        </Grid>
                        <Grid item >
                            <Button className={classes.btns} onClick={handleClose} variant='contained' size='large'>Create campaign</Button>
                        </Grid>
                    </Grid>
                </div>
            </Dialog>

        </div>
    );
}

export default withRouter(AvailableTable);