import React, { useEffect, useMemo, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import theme from "../../utils/theme";
import {
    TableContainer, Table, TableHead,
    TableRow,
    TableCell,
    TableSortLabel,
    TableBody, TablePagination,
    Button,
    TextField,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    Box,
    Typography, Dialog, DialogTitle, IconButton, Grid
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Builder from '../../pages/Builder';
import { useDispatch, useSelector } from "react-redux";

const useStyles = makeStyles((theme) => ({
    root: {
        marginBottom: 40
    },
    table: {
        minWidth: 750,
        borderCollapse: 'separate',
        borderSpacing: '0px 5px',

    },
    tblpagination: {
        color: 'white'
    },
    tableRow: {
    },
    tableHead: {
        color: 'grey',
        borderBottom: "none",
        paddingTop: 60,
        paddingBottom: 25
    },
    tblRow: {
        background: '#231C3D',
        '&:nth-child(even)': {
            background: '#292244'
        }
    },
    tableBody: {
        color: 'white',
        borderBottom: "none",
    },
    contentDiv: {
        paddingLeft: 90,
        paddingRight: 90,
        paddingBottom: 40,
        paddingTop: 20,
    },
    valuedivs: {
        marginBottom: 5
    },
    txtTitle: {
        fontWeight: 'bold'
    },
    txtValue: {

    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    detailtxt: {
        fontSize: 24
    },
    divider: {
        width: '100%',
        height: 2,
        background: 'linear-gradient(90deg, #8B63F3 -11.75%, #E8487F 105.01%)',
        marginBottom: 40
    },
    btns: {
        textTransform: 'none',
        width: 200,
        marginTop: 40
    },
    btns1: {
        textTransform: 'none',
        color: 'white',
        borderColor: 'white',
        width: 200,
        marginTop: 40
    },
    statusDiv: {
        borderRadius: 5,
        background: '#7988E8',
        padding: 10,

    },
    statusDivTxt: {
        color: 'white',
        fontSize: 16,
        fontWeight: 400,
    }
}));

const active_data = [{
    topic: 'eCommerce / Delivery times',
    status: 'In progress',
    progress: '73% (33/45)',
    remain: '14h / 5d',
    penalty: '1',
    apy: '15'
},
{
    topic: 'Telecom / data usage',
    status: 'In progress',
    progress: '56% (45/80)',
    remain: '8h / 15d',
    penalty: '0',
    apy: '12'
}]

const headCells = [
    { id: 'topic', numeric: true, disablePadding: true, label: 'Topic' },
    { id: 'status', numeric: false, disablePadding: true, label: 'Status' },
    { id: 'progress', numeric: false, disablePadding: false, label: 'Progress to competion' },
    { id: 'remain', numeric: false, disablePadding: false, label: 'Remaining time' },
    { id: 'penalty', numeric: false, disablePadding: false, label: 'Penalties' },
    // { id: 'value', numeric: false, disablePadding: false, label: 'BTC Value' },
    { id: 'apy', numeric: false, disablePadding: false, label: 'Estimated APY (%)' },
    { id: 'deactivate', numeric: false, disablePadding: false, label: 'Deactiavate' },
];

function EnhancedTableHead(props) {
    const { classes, rowCount } = props;
    return (
        <TableHead className={classes.tableRow}>
            <TableRow>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align={headCell.numeric ? 'left' : 'center'}
                        padding={'default'}
                        className={classes.tableHead}
                    >
                        {headCell.label}
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}


const ActivatedTable = () => {

    const classes = useStyles();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [selected, setSelected] = useState([]);
    const [display, setColor] = React.useState('contents');
    const [open, setOpen] = React.useState(false);
    const [active_data, setActiveData] = React.useState([]);

    const admin = useSelector(state => state.main.admin);
    const customer = useSelector(state => state.main.customer);
    const architect = useSelector(state => state.main.customer);
    const program = useSelector(state => state.main.program);

    async function getList() {
        try {
            const pool = await program.account.poolAccount.fetch(admin.publicKey);
            let campaigns = pool.campaigns;
            for (let z = 0; z < pool.head; z++) {
                const campaignAddr = await program.account.campaign.associatedAddress(campaigns[z]);
                console.log("\tarchitect ", campaigns[z].toBase58(), "\tcreated campaign ", campaignAddr.toBase58());
                console.log("\campaigns ", campaigns[z]);
                const campaign = await program.account.campaign.fetch(campaigns[z].toBase58());
                console.log("\campaign ", campaign);

                setActiveData([]);
                let temp = [...active_data];
                temp.push({
                    topic: new TextDecoder().decode(Uint8Array.from(campaign.subject)),
                    status: '',
                    progress: '',
                    remain: '',
                    penalty: '',
                    apy: ''
                })
                setActiveData(temp);
            }
        } catch (error) {
            console.log('error', error)
        }
    }
    useEffect(() => {

        if (program != null) {
            getList();
        }
    }, [program])

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1),
            );
        }

        setSelected(newSelected);
    };
    return (
        <div className={classes.root}>
            <TableContainer>
                <Table
                    className={classes.table}
                    aria-labelledby="tableTitle"
                    size='medium'
                    aria-label="enhanced table"
                >
                    <EnhancedTableHead
                        classes={classes}
                        rowCount={active_data.length}
                    />
                    <TableBody>
                        {
                            active_data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
                                const labelId = `enhanced-table-checkbox-${index}`;
                                return (
                                    <TableRow
                                        hover
                                        onClick={(event) => handleClick(event, row.coin)}
                                        role="checkbox"
                                        tabIndex={-1}
                                        key={index}
                                        className={classes.tblRow}
                                    >
                                        <TableCell align="left" className={classes.tableBody}>{row.topic}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}><div className={classes.statusDiv}><Typography className={classes.statusDivTxt}>{row.status}</Typography></div></TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.progress}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.remain}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.penalty}</TableCell>
                                        <TableCell align="center" className={classes.tableBody}>{row.apy}</TableCell>
                                        <TableCell align="center" className={classes.tableBody} onClick={handleClickOpen}><Button variant='contained' color='primary' size="large">More</Button></TableCell>
                                    </TableRow>
                                )
                            })
                        }
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                className={classes.tblpagination}
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={active_data.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
            <Dialog onClose={handleClose} scroll='body' aria-labelledby="customized-dialog-title" open={open} PaperProps={{
                style: {
                    backgroundColor: '#453A6B',
                    boxShadow: 'none',
                },
            }} maxWidth='md'>
                <DialogTitle disableTypography>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <div className={classes.contentDiv}>
                    <Typography className={classes.detailtxt}>Details</Typography>
                    <div className={classes.divider} />
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Organizer:
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                XYZ Corp
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Industry/domain:
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                Insurance
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Topic subject:
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                This campaign to help XYZ corp better engage with users/customers about insurance rates, specifically the

                                differences between flat and usage-based rates
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Example seed phrases
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                With my current usage, what is my monthly rate with the usage-based plan?
                                How much will I save with a usage-based plan?
                                Can I switch back to a flat rate plan?
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Reward per utterance:
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                0.5 SNS
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Total utterances to complete:
                            </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                60
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Estimated APY:  </Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                10%
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Time limit (days):</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                14
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Staking required:</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                12 SNS
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Slashing penalty:</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                0.5 SNS
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.valuedivs}>
                        <Grid item xs={4}>
                            <Typography className={classes.txtTitle}>
                                Validations required:</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <Typography className={classes.txtValue}>
                                2
                            </Typography>
                        </Grid>
                    </Grid>
                    <Grid container direction='row' justify="center" spacing={3}
                        alignItems="center" className={classes.valuedivs}>
                        <Grid item>
                            <Button className={classes.btns1} onClick={handleClose} variant='outlined' size='large'>Close</Button>
                        </Grid>
                        <Grid item >
                            <Button className={classes.btns} onClick={handleClose} variant='contained' size='large' color='primary'>Unstake</Button>
                        </Grid>
                    </Grid>
                </div>
            </Dialog>

        </div>
    );
}

export default ActivatedTable;